import { FileLoader } from 'three'
import { Bag } from "@foxglove/rosbag";

import { BlobReader } from '@foxglove/rosbag/web'

// import {getTime} from './util' 

import { toMillis } from '@foxglove/rostime'

class playWorker{
    // player
    playFile
    loader = new FileLoader()
    playTime
    playState = false
    dataPool = []
    dataCache = {}
    lastTime = new Date().valueOf()
    reader
    ctx
    lastSendTime = 0
    startTime = 0
    done = false
    constructor(){
        this.loader.setResponseType('blob')
        /* eslint-disable-next-line no-restricted-globals */
        this.ctx = self
        this.ctx.onmessage = (e) => {
            const {type,params,id} = e.data
            switch (type) {
                case 'open':
                    // console.log('open',params)
                    this.open(params.file,id)
                    break;
                case 'play':
                    this.playState = true
                    break;
                case 'stop':
                    this.playState = false
                    break;
                default:
                    break;
            }
        }
        this.play()
    }
    async open(file,id){
        this.playFile = file
        // const bRes = await this.loader.loadAsync( bagPath )
        const bag = new Bag( new BlobReader(file) )
        await bag.open()
        console.log('bag', file)
        const topics = []
        bag.connections.forEach( value => {
            topics.push({ topic: value.topic, dataType: value.type })
        } )
        this.playTime = toMillis(bag.startTime)
        this.startTime = toMillis(bag.startTime)
        const bagInfo = {
            startTime: bag.startTime,
            endTime: bag.endTime,
            topics
        }
        this.ctx.postMessage({ type: 'ready', id, params: bagInfo })
        // this.player = bag
        this.reader = bag.messageIterator()
        this.done = false
        this.play()
    }
    async play(){ 
        if( this.playState ){
            const { value, done } = await this.reader?.[Symbol.asyncIterator]().next()
            if(done){
                this.done = true
            } else if(value) {
                const result = value
                const playTime = toMillis(result.timestamp) 
                if(this.lastSendTime===0){
                    this.lastSendTime = playTime
                }
                this.dataCache[result.topic] = result.message
                if(playTime - this.playTime > 20 ){
                    this.dataPool.push({ playTime, data: this.dataCache})
                    this.playTime = playTime
                    this.dataCache = {}
                }
            }
            const now = new Date().valueOf()
            const c = now - this.lastTime
            if( this.dataPool[0] && this.lastSendTime + c > this.dataPool[0].playTime){
                const item = this.dataPool.shift()
                this.lastSendTime = item.playTime
                this.lastTime = now
                this.ctx.postMessage({ type: 'message', params: item })
            }
        }
        if(this.dataPool[0] || !this.done){
            setTimeout(()=>{
                this.play()
            })
        } else {
            this.playState = false
            this.ctx.postMessage({ type: 'end' })
            this.reader = null
            this.lastSendTime = 0
            this.open(this.playFile,0)
        }
        
    }
}

new playWorker()